.App {
  text-align: center;
  padding: 2% 5%;
}

.App * {
  box-sizing: border-box;
}

.city {
  width: 250px;
}

.CitySearch {
  display: flex;
  flex-direction: column;
}

.details-btn {
  float: right;
  margin-top: 10px;
  margin-right: 10px;
  white-space: nowrap;
  padding: 5px 8px;
  border-radius: 4px;
  outline: none;
  cursor: pointer;
  background-color: transparent;
}

.Event {
  padding: 10px 10px 55px;
  margin-bottom: 10px;
  text-align: left;
  border: 1px solid #ccc;
  border-radius: 4px;
  position: relative;
}

.eventName {
  font-size: 18px;
  font-weight: 600;
}

.groupName {
  text-transform: uppercase;
  font-size: 14px;
}

.going {
  font-style: italic;
}

.hideDetails {
  display: none;
}

input[type="text"], input[type="number"] {
  padding: 8px 6px 8px 10px;
  border-radius: 4px;
  outline: none;
  border: 1px solid #ccc;
  font-size: 14px;
  line-height: 20px;
  align-self: center;
  height: 38px;
}

.number-of-events {
  width: 80px;
  margin: 20px 0 50px;
  text-align: right;
}

p {
  margin: 5px 0;
}

.suggestions {
  width: 250px;
  align-self: center;
  margin: 0;
  font-size: 14px;
}

.suggestions > li {
  text-align: left;
  padding: 8px 6px 8px 10px;
  background-color: #eee;
  cursor: default;
}

.suggestions > li:last-child {
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
}

.suggestions > li:hover {
  background-color: #BCDCDB;
}

ul {
  padding: 0;
  list-style: none;
}